@import '../../styles/_variables';
@import '../../styles/_mixins';
.form-control {
    height: 46px;
    padding: 15px 14px;
    border: none;
    outline: 0;
    font-family: $font-calibri-regular; }
footer#app-footer {
    .footer-inner {
        .upper {
            background-color: $dark-gray;
            padding-top: 26px;
            padding-bottom: 40px;
            .upper-inner {
                .item {
                    h2 {
                        font-family: $font-optima;
                        font-size: 18px;
                        font-weight: lighter;
                        color: $darker-gray; }

                    ul {
                        list-style-type: none;
                        li {
                            cursor: pointer;
                            font-family: $font-calibri-regular;
                            font-size: 14px;
                            line-height: 24px;
                            font-weight: lighter;
                            color: $darker-gray; } }
                    .form {
                        h2 {
                            font-size: 20px; }
                        .form-group {
                            display: flex;
                            margin-top: 15px;
                            @include tablet {
                                flex-direction: column;
                                text-align: center; }
                            @include mobile {
                                flex-direction: column;
                                text-align: center; }
                            input {
                              @extend .form-control;
                              flex-grow: 1; }
                            button {
                                @extend .form-control;
                                background-color: $light-eastern-blue;

                                color: #fff;
                                flex-grow: 0.5;
                                display: flex;
                                justify-content: center;
                                align-items: center;
                                @include tablet {
                                    margin-top: 20px;
                                    width: 100px; }
                                @include mobile {
                                    margin-top: 20px;
                                    width: 100px; } } } } } } }

        .lower {
            background-color: $dark-eastern-blue;
            padding: 25px 0;
            .lower-inner {
                display: flex;
                justify-content: space-between;
                text-align: initial;
                @include tablet {
                    flex-direction: column;
                    text-align: center; }
                @include mobile {
                    flex-direction: column;
                    text-align: center; }
                .left {
                    span {
                        font-family: $font-calibri-regular;
                        display: inline-block;
                        color: #fff;
                        font-size: 16px;
                        border-right: 1px solid #fff;
                        padding-right: 10px;
                        margin-right: 10px;
                        &:last-child {
                            border-right: none;
                            padding-right: 0;
                            margin-right: 0; } } }
                .right {
                    p.copy-rights {
                        color: #ffffff;
                        font-family: $font-calibri-regular;
                        font-size: 16px; } } } } } }
