@import '../../styles/_variables';
@import '../../styles/_mixins';
@mixin programSectionMediaQuery {
    @media screen and ( max-width: 1340px ) and (min-width: 1100px) {
        @content; } }
section#programs {
    padding-top: 26px;
    padding-bottom: 80px;
    background-image: url('../../assets/images/programs.png');
    background-size: cover;
    background-repeat: no-repeat;
    .programs-inner {
        position: relative;
        h2 {
            font-family: $font-optima;
            font-size: 36px;
            text-align: center;
            color: #fff;
            margin-bottom: 8px; }
        p {
            font-family: $font-optima;
            font-size: 24px;
            text-align: center;
            color: #fff; }
        & > p {
            @include tablet {
                margin-bottom: 20px; }
            @include mobile {
                margin-bottom: 20px; } }
        .diamond {
            text-align: center;
            padding-left: 150px;
            padding-bottom: 100px;
            overflow: hidden;
            @include programSectionMediaQuery {
                padding-bottom: 150px; }
            @include tablet {
                padding-left: 0;
                padding-bottom: 0; }
            @include mobile {
                padding-left: 0;
                padding-bottom: 0; }
            .colum-container {
                text-align: center;
                display: inline-block;
                transform: rotate(-45deg);
                margin-right: 45px;
                margin-left: 5px;

                @include tablet {
                    transform: rotate(0);
                    margin-right: 0px;
                    margin-left: 0px;
                    display: flex; }
                @include mobile {
                    transform: rotate(0);
                    margin-right: 0px;
                    margin-left: 0px;
                    display: flex; }
                .item {
                    width: 200px;
                    height: 200px;
                    border: 4px solid #fff;
                    margin-right: 50px;
                    transition: background-color 0.2s linear;
                    cursor: pointer;
                    @include tablet {
                        margin-right: 10px; }
                    @include mobile {
                        margin-right: 10px; }


                    &.last-item {
                        margin-bottom: -35px;
                        margin-left: -12px;
                        @include tablet {
                            margin-bottom: 0px;
                            margin-left: 0px; }
                        @include mobile {
                            margin-bottom: 0px;
                            margin-left: 0px; }
                        @include programSectionMediaQuery {
                            margin-top: -131px;
                            margin-right: 143px;
                            margin-bottom: 0px;
                            margin-left: 0px; } }

                    p {
                        font-family: $font-calibri-regular;
                        font-size: 22px; }
                    @include tablet {
                        width: 100%;
                        height: 220px;
                        margin-bottom: 20px; }
                    @include mobile {
                        width: 100%;
                        height: 220px;
                        margin-bottom: 20px; }
                    &:hover {
                        background-color: $eastern-blue;
                        border-color: $eastern-blue; }
                    &.for-demo {
                        visibility: hidden;
                        @include tablet {
                            display: none; }
                        @include mobile {
                            display: none; }
                        @include programSectionMediaQuery {
                            display: none; } }
                    &-inner {
                        transform: rotate(45deg);
                        text-align: center;
                        width: 100%;
                        height: 100%;
                        @include tablet {
                            transform: rotate(0);
                            padding-top: 15px; }
                        @include mobile {
                            transform: rotate(0);
                            padding-top: 15px; }
                        img {
                            // width: 84px
 }                            // height: 57px
                        p {
                            font-size: 28px;
                            font-family: $font-calibri-regular;
                            margin-top: 19px; }
                        i {
                            color: #ffffff;
                            margin-top: 20px; } } }
                .bottom {
                    margin-top: 11px;
                    @include tablet {
                        margin-top: 0px; }
                    @include mobile {
                        margin-top: 0px; } }

                .up {
 } } } } }                    // margin-bottom: 15px

